.contactPage {
    max-width: 600px;
    margin: 50px auto;
    padding: 20px;
    font-family: 'Roboto', Arial, sans-serif;
    color: var(--text-color);
    box-sizing: border-box; /* Ensure padding and borders are included in width calculation */
}

.contactPage h1 {
    font-size: var(--header-font-size);
    font-weight: var(--header-font-weight);
    color: var(--header-color); /* Use header color */
    text-align: var(--header-text-align);
    margin-bottom: var(--header-margin-bottom);
}

/* Form structure */
.form-group {
    margin-bottom: 20px;
    width: 100%; /* Ensure all inputs use full width */
    box-sizing: border-box;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
    font-size: 1rem;
    color: var(--text-color);
}

.form-input,
.form-textarea,
.submit-button {
    width: 100%; /* Make all inputs and buttons span the full width */
    padding: 10px;
    border: 1px solid var(--border-color);
    border-radius: 4px;
    background-color: var(--card-background);
    color: var(--text-color);
    font-size: 1rem;
    line-height: 1.6;
    box-sizing: border-box; /* Include padding and borders in width calculation */
}

.form-textarea {
    height: 100px; /* Default height for message box */
    resize: vertical; /* Allow resizing vertically */
}

.form-input:focus,
.form-textarea:focus {
    outline: none;
    border-color: var(--accent-color);
}

.submit-button {
    cursor: pointer;
    background-color: var(--accent-color);
    color: var(--text-color);
    border: none;
    transition: background-color 0.3s ease;
    text-align: center;
}

.submit-button:hover {
    background-color: var(--hover-color);
}

.submit-button:disabled {
    background-color: #ccc;
    color: #999;
    cursor: not-allowed;
}
