/* General Page Styles */
.resumePage {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 80px; /* Horizontal padding for gaps */
  color: var(--text-color);
  font-family: 'Roboto', Arial, sans-serif;
  line-height: 1.4;
}

.resumeHeader {
  text-align: center;
  margin-bottom: 30px;
}

.resumeHeader h1 {
  font-size: 2.5rem;
  color: var(--header-color);
}

/* Section Styles */
.section {
  margin-bottom: 24px;
  padding: 40px 40px;
  background-color: var(--card-background);
  border-radius: px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.sectionTitle {
  font-size: 1.8rem;
  color: var(--accent-color);
  margin-bottom: 20px;
  text-align: left;
}

/* Skills List */
.skillsList {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding: 0;
  margin: 0;
  list-style: none;
}

.skillsList li {
  flex: 1 1 30%; /* 3 columns for larger screens */
  text-align: left;
  font-size: 1rem;
}

/* Work Experience Section */
.section {
  margin: 40px 0;
}

/* Heading */
.section h2 {
  margin-bottom: 20px;
  font-size: 1.8rem; /* Adjust heading font size */
}

/* Job Title Styling */
.jobTitle {
  display: flex;
  justify-content: space-between;
  font-size: 1.2rem; /* Adjust job title font size */
  font-weight: bold;
  margin-bottom: 5px;
}

/* Company Info */
.company {
  font-size: 1rem; /* Adjust company font size */
  margin-bottom: 10px;
}

/* List Styling */
ul {
  margin: 0;
  padding: 0 20px;
  list-style-type: disc;
  font-size: 0.95rem; /* Adjust bullet point font size */
  line-height: 1.5;
}


/* Education & Certifications */
.section p {
  margin: 0 0 10px 0;
  font-size: 1rem;
  text-align: left;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .resumePage {
      padding: 0 20px; /* Reduce padding for smaller screens */
  }

  .skillsList li {
      flex: 1 1 45%; /* 2 columns on medium screens */
  }
}

@media (max-width: 768px) {
  .resumePage {
      padding: 0 10px; /* Even smaller padding for narrow screens */
  }

  .resumeHeader h1 {
      font-size: 2rem; /* Smaller font size for the title */
  }

  .sectionTitle {
      font-size: 1.5rem; /* Adjust section title size */
  }

  .skillsList li {
      flex: 1 1 100%; /* Single column on small screens */
  }

  .jobTitle {
      font-size: 1.2rem; /* Reduce font size for job title */
  }

  .job ul li {
      font-size: 0.9rem; /* Adjust font size for bullet points */
  }
}
