.aboutPage {
    max-width: 800px; /* Center the content and limit width */
    margin: 0 auto;
    padding: 20px;
    font-family: 'Roboto', Arial, sans-serif;
    color: var(--text-color);
    line-height: 1.6;
}

/* Top Section with Title, Picture, and Description */
.topSection {
    display: flex;
    align-items: center; /* Vertically align picture and text */
    gap: 24px; /* Add 24px spacing between the profile picture and text */
    margin-bottom: 40px;
}

.textContainer h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
    color: var(--header-color);
    text-align: left; /* Ensure text aligns to the left */
    margin-left: 0; /* Remove any additional left margin */
}


.profilePicture {
    width: 200px; /* Larger size for better visibility */
    height: auto; /* Maintain aspect ratio */
    border-radius: 8px; /* Slightly rounded corners */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-top: 20px; /* Lower the profile picture */
}

.textContainer h1 {
    font-size: 2.5rem;
    margin-bottom: 15px;
    color: var(--header-color);
}

.textContainer p {
    font-size: 1.2rem;
    color: var(--text-color);
}

/* Long Paragraph Section */
.longParagraph {
    margin: 40px 0;
    text-align: left;
}

.longParagraph p {
    font-size: 1.1rem;
    line-height: 1.8; /* Increase readability */
}

/* Bottom Section with Signature Image */
.signatureSection {
    text-align: right; /* Align signature to the right */
    margin-top: 40px;
}

.signatureImage {
    width: 200px; /* Larger signature */
    height: auto; /* Maintain aspect ratio */
    border-radius: 0; /* No circular styling */
    margin-right: 10px; /* Add slight spacing from the right edge */
}
